import React from 'react';
import ReactDOM from 'react-dom';

import { Button, Navbar, Form, FormControl, Nav, NavDropdown } from 'react-bootstrap';

import Home from './Home';
import MainContainer from './MainContainer';

import { HashRouter as Router, Switch, Route, useParams, } from "react-router-dom";

import { createBrowserHistory } from 'history';

import 'bootstrap/dist/css/bootstrap.min.css';

export const history = createBrowserHistory({
    basename: process.env.PUBLIC_URL
});

function MainApp(){
    return (
        <div>
            <Router basename={process.env.PUBLIC_URL}>
                <Navbar bg="dark" variant="dark" expand="lg" sticky="top">
                    <Navbar.Brand href="#home">React Bootstrap Navbar</Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="mr-auto">
                            <Nav.Link href={`${process.env.PUBLIC_URL}/`}>Home</Nav.Link>
                            <Nav.Link href={`${process.env.PUBLIC_URL}/MainContainer`}>Main 1</Nav.Link>
                            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                                <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                                <NavDropdown.Divider />
                                <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                            </NavDropdown>
                        </Nav>
                        <Form inline>
                            <FormControl type="text" placeholder="Search" className="mr-sm-2" />
                            <Button variant="outline-success">Search</Button>
                        </Form>
                    </Navbar.Collapse>
                </Navbar>
                <br />
                <Switch>
                    <Route exact path={`${process.env.PUBLIC_URL}/`} component={Home}/>
                    <Route exact path={`${process.env.PUBLIC_URL}/MainContainer`} component={MainContainer}/>
                    {/* <Route exact path="/">
                        <Home />
                    </Route>
                    <Route path={`${process.env.PUBLIC_URL}/MainContainer`}>
                        <MainContainer />
                    </Route> */}
                </Switch>
            </Router>

        </div>
    );
}

export default MainApp;